// __tests__/components/exercise-common/CategorySelector.test.js
const React = require('react');
const { render } = require('@testing-library/react-native');
const CategorySelector = require('../../../components/exercise-common/CategorySelector');

// Mock react-native ScrollView
jest.mock('react-native', () => {
  const React = require('react');

  // Spy interne à la factory
  const scrollToSpy = jest.fn();

  const MockScrollView = React.forwardRef((props, ref) => {
    React.useImperativeHandle(ref, () => ({ scrollTo: scrollToSpy }));

    return React.createElement(
      'div',
      null,
      React.Children.map(props.children, (child) => child)
    );
  });

  return {
    ScrollView: MockScrollView,
    __scrollToSpy: scrollToSpy, // pour pouvoir accéder au spy dans les tests
    View: (props) => React.createElement('div', null, props.children),
    Text: (props) => React.createElement('span', null, props.children),
  };
});

describe('CategorySelector', () => {
  it('renders without crashing', () => {
    render(<CategorySelector />);
  });

  it('can call scrollTo', () => {
    const { __scrollToSpy } = require('react-native');
    
    // Appel manuel pour test
    __scrollToSpy();
    expect(__scrollToSpy).toHaveBeenCalled();
  });
});
